@import "vars.scss";

html,
body {
  height: 100%;
}

body {
  min-width: 350px;
  @extend %default-text;
}

a {
  @extend %default-text;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}
.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1054px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 20px;
}
